import { useCallback, useEffect, useState } from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from "react-chartjs-2";
import { LogCliService } from "../../../services/adminService";
export default function LogActionChartComponent(props: any) {
    const [data, setData] = useState([]);

    let loadData = useCallback(() => {
        LogCliService.GetChartData((res: any) => {
            console.log(res.data.data);
            setData(res.data.data)
        })
    }, []);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function renderChart() {
        console.log(data);
        
        return data.map((data: any, index: number) => {
            return (
                <div key={index} style={{ height: 500, width: 500 }}>
                    <Pie data={data} />
                </div>
            );
        })
    }
    ChartJS.register(ArcElement, Tooltip, Legend);
    return (
        <div>
            {renderChart()}
        </div>
    );
}