import { Col, Form, Row } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { LogCliService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";
import LogActionChartComponent from "./LogActionChartComponent";

export default function LogActionComponent() {
    const [data, setData] = useState([]);
    const [dateFrom, setDateFrom] = useState(moment());
    const [dateTo, setDateTo] = useState(moment());
    const [isLoading, setIsLoading] = useState(false);

    const GenDateString = useCallback(() => {
        return `dateFrom=${dateFrom.format("yyyy-MM-DD")}&dateTo=${dateTo.format("yyyy-MM-DD")}`;
    }, [dateFrom, dateTo]);

    var loadData = useCallback(() => {
        setIsLoading(true);
        LogCliService.GetListLogAction(GenDateString(), (data: any) => {
            var row = data.data.data.map((data: any, index: number) => {
                return {
                    key: index + 1,
                    httpRequest: data.httpRequest,
                    date: data.date,
                    ipAddress: data.ipAddress,
                    userName: data.userName
                };
            })
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        });
    }, [GenDateString]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function RenderData() {
        return data.map((row: any, index: number) => {
            return (
                <div key={index}>
                    <p>Username: <strong>{row.userName}</strong> - Date: <UIFormatDateTimeVNComponent value={row.date} /> - Ip Address: {row.ipAddress}</p>
                    <p>Path: <strong>{row.httpRequest.method}</strong> {row.httpRequest.path}{row.httpRequest.queryString}</p>
                    <hr />
                </div>
            );
        });
    }

    function HandleDeleteLog() {
        setIsLoading(true);
        LogCliService.DeleteLogAction(GenDateString(), (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }
    return (
        <MyBox
            title="Log hành động người dùng"
            isLoading={isLoading}>
            <Form layout="horizontal">
                <Row gutter={12}>
                    <Col span={12}>
                        <MyFormItem.DatePicker
                            label="Từ ngày"
                            value={dateFrom}
                            setValue={setDateFrom} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.DatePicker
                            label="Đến ngày"
                            value={dateTo}
                            setValue={setDateTo} />
                    </Col>
                    <Col span={12}>
                        <MyButton onClick={HandleDeleteLog} value="Xóa log" />
                    </Col>
                </Row>
            </Form>

            <LogActionChartComponent />
            {RenderData()}
        </MyBox>
    );
}